.button-container {
  padding: 10px;
  border-radius: 5px;
  background: linear-gradient(262.71deg, #00d1ff -30.04%, #00ffa3 124.74%);
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  width: 100px;
}

.button-container:hover {
  cursor: pointer;
  background: linear-gradient(262.71deg, #0dcbf6 -30.04%, #1bbd82 124.74%);
}
